import React, { useCallback, useState } from "react"
import Container from "../../elements/container/container"
import IllustrationBackground from "../../background/illustrationbackground/background"
import CopyLink from "../../components/copylink"
import VbDropZone from "../../components/dropzone"
import Seperator from "../../elements/seperator/seperator"
import Title from "../../elements/containertitle/title"
import { i18n } from "../../i18n/language"
import { uploadFile } from "../../cloudinary"

function MyDropzone() {
  const [link, setLink] = useState(null)
  const [percent, setPercent] = useState(null)

  const onDrop = useCallback(images => {
    var id = Math.random().toString(36).substring(7);
    var folder = new URLSearchParams(window.location.search).get("folder");
    uploadFile({ files: [images[0]] }, (res) => setLink(`${window.location.origin}/collaboration/file/?workspace=${res.public_id}`),
        folder,id, (percent) => setPercent(percent))
  })

  return <div>
    <div className={"dropzone-wrapper"}>
      <IllustrationBackground>
        <Container>

          <Seperator/>

          <Title title={`${i18n.collaboration.upload.title} ${percent ? percent + "/100%" : ""}`}
                 subtitle={"Please upload PDF here"}/>
          <VbDropZone onDrop={onDrop}/>
          {link && <CopyLink link={link}/>}
        </Container>

        <Seperator/>

      </IllustrationBackground>
    </div>
  </div>
}

export default MyDropzone