export function uploadFile(file, onDone,folder, public_id, onPercent) {


  return new Promise(function (resolve, reject) {
    console.log(public_id);
    var url = `https://api.cloudinary.com/v1_1/hvioxpubt/upload?upload_preset=ml_default`
    var xhr = new XMLHttpRequest()
    var fd = new FormData()

    fd.append("file", file.files[0])

    xhr.open("POST", url, true)
    xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest")

    xhr.upload.onprogress = function(e) {
      var percentComplete = Math.ceil((e.loaded / e.total) * 100)
      onPercent && onPercent(percentComplete);
    }


    xhr.onreadystatechange = function(e) {
      if (xhr.readyState === 4 && xhr.status === 200) {
        var response = JSON.parse(xhr.responseText)
        onDone(response);
        resolve();
      }
    }


    fd.append("tags", "browser_upload") // Optional - add tag for image admin in Cloudinary

    if(folder){
      fd.append("folder",folder)
    }

    if(public_id){
      fd.append("public_id", public_id);
    }


    xhr.send(fd);

    xhr.onerror = reject;
  });

}